<template>
  <div class="merchantCenter">
    <Navbar></Navbar>
    <div class="merchantCenterMain">
      <div class="merchantCenterMainHeader"></div>

      <div class="merchantCenterMainContent">
        <div class="notice">
          <div class="jcc">
            <img src="../../assets/img/pic11.png" alt="" />
          </div>
          <van-notice-bar
            background="#fff"
            scrollable
            :text="$t('index.text74')"
          />
        </div>

        <div class="merchantInfo mt2r">
          <div class="merchantName">{{ $t("index.text75") }}</div>
          <div class="creditScore mt2r">
            <div style="display: flex; align-items: center">
              <img src="../../assets/img/pic3.png" alt="" />
              <span style="font-weight: bold; font-size: 1.6rem">{{
                userInfo.shop_name
              }}</span>
            </div>
          </div>

          <div class="line"></div>

          <div class="withdraw jcsb">
            <div class="left">
              <div class="text">{{ $t("index.text76") }}</div>
              <div class="hf">{{ hf }}</div>
            </div>
            <div class="right jcc" @click="withdrawalPopup = true">
              {{ $t("index.text77") }}
            </div>
          </div>

          <div class="creditScore1 jcsb">
            <div>{{ $t("index.text28") }}:{{ userInfo.credits }}</div>
            <div
              class="jcc"
              style="color: #666666"
              @click="creditScorePopup = true"
            >
              {{ $t("index.text78") }}<van-icon name="arrow" />
            </div>
          </div>
        </div>

        <div class="release jcsb mt2r">
          <div
            class="releaseItem"
            @click="
              $router.push({
                path: '/publishProducts',
                query: {
                  productInfo: JSON.stringify({
                    productId: '',
                    status: true
                  })
                }
              })
            "
          >
            <img
              src="../../assets/img/pic12.png"
              alt=""
              style="width: 2.35rem; height: 2.35rem"
            />
            <span> {{ $t("index.text79") }}</span>
          </div>

          <div class="releaseItem" @click="$router.push('/productManagement')">
            <img
              src="../../assets/img/pic13.png"
              alt=""
              style="width: 2.35rem; height: 2.35rem"
            />
            <span> {{ $t("index.text80") }}</span>
          </div>
        </div>

        <div
          class="merchantOrders jcsb mt2r"
          @click="$router.push('/merchantOrders')"
        >
          <div class="jcc">
            <img src="../../assets/img/pic14.png" alt="" />
            <span> {{ $t("index.text81") }}</span>
          </div>

          <van-icon name="arrow" />
        </div>
      </div>
    </div>

    <van-popup v-model="withdrawalPopup" class="withdrawalPopup">
      <div class="content">
        <img
          src="../../assets/img/close.png"
          class="close"
          alt=""
          @click="(withdrawalPopup = false), (quantity = '')"
        />
        <div class="title">HF {{ $t("index.text77") }}</div>

        <img src="../../assets/img/pic6.png" class="pic6" alt="" />
        <img src="../../assets/img/pic7.png" class="pic7" alt="" />
        <div class="contentBox">
          <div class="balance mt2r">
            {{ $t("index.text76") }}
            <span style="font-weight: bold">{{ hf }}</span>
          </div>

          <input
            type="text"
            placeholder="请输入HF数量"
            v-model.trim="quantity"
            class="hfInput mt2r"
          />

          <div class="pic16 jcc mt2r">
            <img src="../../assets/img/pic16.png" alt="" />
          </div>

          <input
            type="text"
            placeholder="USDT"
            v-model="purchaseQuantity"
            class="hfInput mt2r"
            disabled
          />

          <div class="confirm jcc" @click="withdrawal">
            {{ $t("index.text10") }}
          </div>

          <div class="tips mt2r">{{ $t("index.text82") }}：100HF=1USDT</div>
          <div class="tips mt2r">{{ $t("index.text83") }}</div>

          <div class="tips1 jcc mt2r">
            <span @click="$router.push('/withdrawalRecords')">
              {{ $t("index.text84") }}
            </span>
          </div>
        </div>
      </div>
    </van-popup>

    <van-popup v-model="creditScorePopup" class="withdrawalPopup">
      <div class="content">
        <img
          src="../../assets/img/close.png"
          class="close"
          alt=""
          @click="creditScorePopup = false"
        />
        <div class="title">{{ $t("index.text85") }}</div>
        <img
          src="../../assets/img/pic6.png"
          class="creditScorePopupPic6"
          alt=""
        />
        <img src="../../assets/img/pic7.png" class="pic7" alt="" />

        <div class="tips mt2r">
          {{ $t("index.text86") }}
        </div>
      </div>
    </van-popup>
  </div>
</template>

<script>
import BigNumber from "bignumber.js";

export default {
  components: {},
  data() {
    return {
      withdrawalPopup: false,
      creditScorePopup: false,
      merchantName: "",
      hf: 0,
      userInfo: {},
      quantity: ""
    };
  },
  activated() {
    this.getBalance();
    this.getUserInfo();
  },
  computed: {
    purchaseQuantity() {
      if (this.quantity) {
        const price = new BigNumber(0.01);
        const quantity = new BigNumber(this.quantity);
        return quantity.times(price);
      } else return "HL";
    }
  },
  methods: {
    async getUserInfo() {
      this.$api.get("user/info", {}).then(res => {
        this.userInfo = res.data;
      });
    },
    async getBalance() {
      let hf = await this.core
        .hfContract()
        .balanceOf(this.userAddress)
        .call();
      this.hf = this.fromWei(hf);
    },
    async withdrawal() {
      try {
        if (!this.quantity) {
          this.$toast(this.$t("index.text87"));
          return;
        }

        if (Number(this.quantity) > Number(this.hf)) {
          this.$toast(this.$t("index.text88"));
          return;
        }
        let gasPrice = await this.getGasPrice();

        let allowance = await this.core
          .hfContract()
          .allowance(this.userAddress, this.core.coreAddress)
          .call();

        let a = Number(this.fromWei(allowance));
        let b = Number(this.quantity);
        this.$toast.loading({
          duration: 0,
          forbidClick: true,
          message: this.$t("index.text21"),
          className: "loading"
        });
        if (a < b) {
          let gas = await this.core
            .hfContract()
            .approve(this.core.coreAddress, this.toWei(this.quantity))
            .estimateGas();

          await this.core
            .hfContract()
            .approve(this.core.coreAddress, this.toWei(this.quantity))
            .send({
              from: this.$store.state.userAddress,
              gas: gas + 5000,
              gasPrice: gasPrice
            });
        }
        let gas1 = await this.core
          .coreContract()
          .claimPoints(this.toWei(this.quantity))
          .estimateGas();
        await this.core
          .coreContract()
          .claimPoints(this.toWei(this.quantity))
          .send({
            from: this.userAddress,
            gas: gas1 + 10000,
            gasPrice: gasPrice
          });
        this.getBalance();
        this.$toast(this.$t("index.text89"));
      } catch (error) {
        this.$toast.clear();
        this.setErrDialog(error);
      }
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.merchantCenter {
  height: 100%;
  overflow: scroll;
  display: flex;
  flex-direction: column;
  height: 100%;
}
.merchantCenterMain {
  flex: 1;
  position: relative;
  background: #f6f6f6;
}
.merchantCenterMainHeader {
  position: absolute;
  top: 0;
  width: 100%;
  height: 12rem;
  background: linear-gradient(134deg, #d12c21 0%, #f5c96d 100%);
}

.merchantCenterMainContent {
  padding: 2rem 1.5rem;
  z-index: 9;
  position: relative;

  .notice {
    background: #ffffff;
    display: flex;
    border-radius: 0.5rem;
    padding: 0 1.5rem;
    img {
      width: 2rem;
      height: 2rem;
    }
    .van-notice-bar {
      flex: 1;
    }
    /deep/ .van-notice-bar__content {
      color: #d12c21 !important;
    }
  }

  .merchantInfo {
    padding: 1.4rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background: #ffffff;
    border-radius: 0.5rem;

    .merchantName {
      font-weight: bold;
    }

    .creditScore {
      img {
        width: 1.6rem;
        height: 1.6rem;
        margin-right: 0.5rem;
      }
    }

    .line {
      height: 0.1rem;
      margin: 1.4rem 0;
      background: #dddddd;
    }

    .withdraw {
      .left {
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        .hf {
          margin-top: 1rem;
          width: 20rem;
          font-size: 2rem;
          font-weight: bold;
          color: #d12c21;
        }
      }

      .right {
        height: 3.3rem;
        padding: 0 2rem;
        border-radius: 2rem;
        color: #fff;
        font-size: 1.6rem;
        background: linear-gradient(134deg, #d12c21 0%, #f5c96d 100%);
      }
    }

    .creditScore1 {
      margin-top: 3rem;

      .van-icon {
        color: #cecece;
        margin-left: 0.5rem;
      }
    }
  }
  .release {
    .releaseItem {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
      padding: 1.5rem 0;
      width: 16.45rem;
      border-radius: 0.4rem;
      background: linear-gradient(134deg, #d12c21 0%, #f5c96d 100%);

      span {
        margin-top: 1rem;
        font-weight: bold;
        font-size: 1.6rem;
        color: #fff;
      }
    }
  }

  .merchantOrders {
    img {
      width: 2.2rem;
      height: 2.2rem;
    }
    span {
      font-weight: bold;
      font-size: 1.6rem;
      margin-left: 0.5rem;
    }

    .van-icon {
      color: #cdcdcd;
      font-size: 1.6rem;
    }
  }
}
.withdrawalPopup {
  border-radius: 0.5rem;
  width: 90vw;
  background: #fcefd3;
  .tips {
    text-align: center;
    line-height: 2.8rem;
  }
  .content {
    position: relative;
    padding: 2rem;
  }
  .close {
    position: absolute;
    top: 1.3rem;
    right: 2rem;
    width: 3rem;
    height: 3rem;
  }
  .title {
    text-align: center;
    font-size: 1.6rem;
    font-weight: bold;
  }
  .pic6 {
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    width: 5rem;
    height: 3rem;
  }

  .creditScorePopupPic6 {
    position: absolute;
    right: 0;
    bottom: 0.5rem;
    width: 6rem;
    height: 4rem;
  }

  .pic7 {
    position: absolute;
    top: 2rem;
    left: 0;
    width: 6rem;
    height: 2.5rem;
  }
  .contentBox {
    .hfInput {
      width: 100%;
      border: 0;
      height: 5rem;
      border-radius: 1rem;
      padding-left: 1.5rem;
      color: #000;
      box-sizing: border-box;
      position: relative;
      z-index: 9;
    }

    .hfInput::placeholder {
      color: #000;
    }
    .hfInput:disabled {
      background: #fff;
    }

    .pic16 {
      img {
        width: 2rem;
        height: 2rem;
      }
    }

    .tips1 {
      span {
        text-decoration: underline;
      }
    }
  }

  .confirm {
    position: relative;
    z-index: 9;
    margin-top: 2rem;
    height: 5rem;
    background: linear-gradient(134deg, #d12c21 0%, #f5c96d 100%);
    box-shadow: 2px 5px 20px 1px rgba(255, 16, 0, 0.28);
    border-radius: 0.8rem;
    font-size: 1.6rem;
    color: #fff;
    font-weight: bold;
  }
}
</style>



